.content {
    article {
        header {
            h1 {
                font-size: 3.8rem;
                line-height: 4rem;

                @media only screen and (max-width: 768px) {
                    font-size: 3.6rem;
                    line-height: 4rem;
                }
            }
        }
    }

    hr {
        border: none;
        background-color: $bg-color;
        border-top: 3px dotted #ddd;
        color: $bg-color;
    }

    .post {
        .post-image {
            text-align: center;
        }
    }
}

.colorscheme-dark {
    hr {
        border: none;
        background-color: $bg-color-dark;
        border-top: 3px dotted #222;
        color: $bg-color-dark;
    }
}
