body.colorscheme-dark {
    background-color: #151515;
}

pre {
    font-size: 1.4rem;
    line-height: 2rem;
}

code {
    font-size: 1.4rem;
}

p {
    white-space: normal;
    white-space: -moz-normal;
    white-space: -normal;
    white-space: -o-normal;
    margin: 1rem 0 1rem 0;
}

table td,
table th {
    border: 1px solid $alt-fg-color;
}
