$bg-color-quote2: #eee !default;
$bg-color-quote2-note: #7e57c21a !default;
$bg-color-quote2-tip: #26a69a1a !default;
$bg-color-quote2-example: #8d6e631a !default;
$bg-color-quote2-question: #9ccc651a !default;
$bg-color-quote2-info: #42a5f51a !default;
$bg-color-quote2-warning: #ffca281a !default;
$bg-color-quote2-error: #ef53501a !default;

$border-color-quote2: #aaaaa !default;
$border-color-quote2-note: #5e35b1 !default;
$border-color-quote2-tip: #00897b !default;
$border-color-quote2-example: #6d4c41 !default;
$border-color-quote2-question: #7cb342 !default;
$border-color-quote2-info: #1e88e5 !default;
$border-color-quote2-warning: #ffb300 !default;
$border-color-quote2-error: #e53935 !default;

.quote2 {
    margin: 20px auto;
    font-style: normal;
    padding: 2rem 4rem 2rem 4rem;
    border-left: 8px solid $border-color-quote2;
    position: relative;
    line-height: 3.2rem;
}

.quote2-note {
    background-color: $bg-color-quote2-note;
    border-left-color: $border-color-quote2-note;
}
.quote2-tip {
    background-color: $bg-color-quote2-tip;
    border-left-color: $border-color-quote2-tip;
}
.quote2-example {
    background-color: $bg-color-quote2-example;
    border-left-color: $border-color-quote2-example;
}
.quote2-question {
    background-color: $bg-color-quote2-question;
    border-left-color: $border-color-quote2-question;
}
.quote2-warning {
    background-color: $bg-color-quote2-warning;
    border-left-color: $border-color-quote2-warning;
}
.quote2-error {
    background-color: $bg-color-quote2-error;
    border-left-color: $border-color-quote2-error;
}

.quote2:not(.no-quote)::before {
    font-family: Arial;
    content: "\201C";
    color: #888;
    font-size: 5rem;
    font-style: italic;
    position: absolute;
    left: 10px;
    top: 18px;
}

.quote2:not(.no-quote)::after {
    content: '';
}

.quote2 > .quote2-author {
    display: block;
    color: #888;
    font-style: normal;
    font-weight: bold;
    font-size: 1.5rem;
    margin-top: 0.8em;
    text-align: right;
}
